
import { Link, Route, Switch } from 'react-router-dom';
import './App.css';
import About from './components/About';
import Home from './components/Home';

function App() {
  return (
    <div className="App">
    <Link to='/'><button>Home</button></Link>
    <Link to='/about'><button>About</button></Link>

    <Switch>
      <Route exact path="/">
        <Home/>
      </Route>
      <Route path="/about">
        <About/>
      </Route>
    </Switch>
    </div>
  );
}

export default App;
